.country-info {
    display: block;
    margin-top: 5px;
    font-size: 14px;
    color:aqua;
  }
  .country-flag {
    width: 50px;
    height: 30px;
    margin-right: 10px;
  }
  .country-item {
    display: flex;
    align-items: center;
    background-color:#1c1c1c;
    padding: 10px;
    margin-bottom: 1px;
    border-radius: 4px;
    box-shadow: 0 1px 1px #ffffff;
  }

  .body
  {
    background-color: #333333;
  }
  .heading{
    color:white;
      font-size: 30px;
    text-align: center;
  }
  .navbar{
    padding: 10px;
        background-color: #333333;
  }
  .load{
    color:white;
  }
  .controls{
     display: flex;
    align-items: center;
  }
  
  .controls input[type="text"] {
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
    margin-right: 10px;
    font-size: 14px;
    flex: 1;
    margin-top: 20px;
   
  }
  .capital-title-link{
    color:white;
    margin-left: 25px;

  }